import { _decorator, Component, Node, Vec3, tween } from 'cc';
import { Plane } from './Plane';
const { ccclass, property } = _decorator;

@ccclass('BulletE')
export class BulletE extends Component {
    public bulletSpeed: number = 10;
    private speed: number = 100;
    start() {

    }

    update(deltaTime: number) {
        
    }
    bulletMove(){
        if(this.node.name == 'enemyb0'){
            let posXEnd = Plane.instance.node.position.x;
            let posYEnd = Plane.instance.node.position.y;
            let posEnd = new Vec3(posXEnd, posYEnd);

            const distance = Vec3.distance(this.node.position, posEnd);
            const duration = distance / this.speed;

            tween(this.node)
            .to(duration, { position: posEnd })
            .call(() => {
                this.bulletMove();
            })
            .start();
        }
    }
}


